/**
 * 
 * @param {Obejct} 
 * @@return {Object} 
 */
export const deepCopy = (val) => {
	return JSON.parse(JSON.stringify(val))
}
/**
 * 
 * @param {String} format 
 * @@return {Boolean} 
 */
export const typeFilter = (format) => {
	return format === 'int' || format === 'double' || format === 'number' || format === 'timestamp';
}

/**
 *  value  undefined
 * @param {String} key 
 * @param {any} value 
 * @param {Object} rules 
 */
export const getValue = (key, value, rules) => {
	const isRuleNumType = rules.find(val => val.format && typeFilter(val.format));
	const isRuleBoolType = rules.find(val => (val.format && val.format === 'boolean') || val.format === 'bool');
	//  number
	if (!!isRuleNumType) {
		if (!value && value !== 0) {
			value = null
		} else {
			value = isNumber(Number(value)) ? Number(value) : value
		}
	}

	//  boolean
	if (!!isRuleBoolType) {
		value = isBoolean(value) ? value : false
	}

	return value;
}

/**
 * 
 * @param {String|Array} name  realName 
 * @param {Object} data 
 * @param {any} value  
 */
export const setDataValue = (field, formdata, value) => {
	formdata[field] = value
	return value || ''
}

/**
 * 
 * @param {String|Array} field  realName 
 * @param {Object} data 
 */
export const getDataValue = (field, data) => {
	return objGet(data, field)
}

/**
 * 
 * @param {String|Array} field  realName 
 */
export const getDataValueType = (field, data) => {
	const value = getDataValue(field, data)
	return {
		type: type(value),
		value
	}
}

/**
 * name
 * @param {String|Array} name name
 * @@return {String} name
 */
export const realName = (name, data = {}) => {
	const base_name = _basePath(name)
	if (typeof base_name === 'object' && Array.isArray(base_name) && base_name.length > 1) {
		const realname = base_name.reduce((a, b) => a += `#${b}`, '_formdata_')
		return realname
	}
	return base_name[0] || name
}

/**
 * name
 * @param {String|Array} name name
 * @@return {String} name
 */
export const isRealName = (name) => {
	const reg = /^_formdata_#*/
	return reg.test(name)
}

/**
 * 
 * @@return {Object|Array} object 
 */
export const rawData = (object = {}, name) => {
	let newData = JSON.parse(JSON.stringify(object))
	let formData = {}
	for(let i in newData){
		let path = name2arr(i)
		objSet(formData,path,newData[i])
	}
	return formData
}

/**
 * name array
 * @param {*} name 
 */
export const name2arr = (name) => {
	let field = name.replace('_formdata_#', '')
	field = field.split('#').map(v => (isNumber(v) ? Number(v) : v))
	return field
}

/**
 * 
 * @param {Object|Array} object 
 * @param {String| Array} path 'a.b.c'  ['a',0,'b','c']
 * @param {String} value 
 */
export const objSet = (object, path, value) => {
	if (typeof object !== 'object') return object;
	_basePath(path).reduce((o, k, i, _) => {
		if (i === _.length - 1) { 
			// 
			o[k] = value
			return null
		} else if (k in o) { 
			// 
			return o[k]
		} else { 
			// 
			o[k] = /^[0-9]{1,}$/.test(_[i + 1]) ? [] : {}
			return o[k]
		}
	}, object)
	// object
	return object;
}

//  path path'a[0].b.c''a.0.b.c'  ['a','0','b','c']
function _basePath(path) {
	// 
	if (Array.isArray(path)) return path
	//  '[',']' '['  '.', ']'
	return path.replace(/\[/g, '.').replace(/\]/g, '').split('.')
}

/**
 * 
 * @param {Object|Array} object 
 * @param {String| Array} path 'a.b.c'  ['a',0,'b','c']
 * @param {String} defaultVal 
 */
export const objGet = (object, path, defaultVal = 'undefined') => {
	// path
	let newPath = _basePath(path)
	// 
	let val = newPath.reduce((o, k) => {
		return (o || {})[k]
	}, object);
	return !val || val !== undefined ? val : defaultVal
}


/**
 *  number  
 * @param {any} num 
 * @return {Boolean}  number
 */
export const isNumber = (num) => {
	return !isNaN(Number(num))
}

/**
 *  boolean  
 * @param {any} bool 
 * @return {Boolean}  boolean
 */
export const isBoolean = (bool) => {
	return (typeof bool === 'boolean')
}
/**
 * 
 * @param {Object} rules 
 * @return {Boolean} 
 */
export const isRequiredField = (rules) => {
	let isNoField = false;
	for (let i = 0; i < rules.length; i++) {
		const ruleData = rules[i];
		if (ruleData.required) {
			isNoField = true;
			break;
		}
	}
	return isNoField;
}


/**
 * 
 * @param {Any} obj 
 */
export const type = (obj) => {
	var class2type = {};

	// class2type
	"Boolean Number String Function Array Date RegExp Object Error".split(" ").map(function(item, index) {
		class2type["[object " + item + "]"] = item.toLowerCase();
	})
	if (obj == null) {
		return obj + "";
	}
	return typeof obj === "object" || typeof obj === "function" ?
		class2type[Object.prototype.toString.call(obj)] || "object" :
		typeof obj;
}

/**
 * 
 * @param {any} a   
 * @param {any} b   
 * @return {Boolean} 
 */
export const isEqual = (a, b) => {
	//ab
	if (a === b) {
		//0-0
		return a !== 0 || 1 / a === 1 / b;
	}
	//nullundefined
	if (a == null || b == null) {
		return a === b;
	}
	//ab
	var classNameA = toString.call(a),
		classNameB = toString.call(b);
	//false
	if (classNameA !== classNameB) {
		return false;
	}
	//
	switch (classNameA) {
		case '[object RegExp]':
		case '[object String]':
			//
			return '' + a === '' + b;
		case '[object Number]':
			//,NaN
			if (+a !== +a) {
				return +b !== +b;
			}
			//0-0
			return +a === 0 ? 1 / +a === 1 / b : +a === +b;
		case '[object Date]':
		case '[object Boolean]':
			return +a === +b;
	}
	//
	if (classNameA == '[object Object]') {
		//ab
		var propsA = Object.getOwnPropertyNames(a),
			propsB = Object.getOwnPropertyNames(b);
		if (propsA.length != propsB.length) {
			return false;
		}
		for (var i = 0; i < propsA.length; i++) {
			var propName = propsA[i];
			//false
			if (a[propName] !== b[propName]) {
				return false;
			}
		}
		return true;
	}
	//
	if (classNameA == '[object Array]') {
		if (a.toString() == b.toString()) {
			return true;
		}
		return false;
	}
}
